import {Diagram, Divider, DownloadButton, Row, TextBlock} from './comps';
import { Section } from './sections';
import gpt3b1b from './diagrams/gpt3b1b.png';
import * as introcontent from './content/intro';
import * as transformercontent from './content/transformers';
import d1 from './diagrams/d1.jpg';
import d2 from './diagrams/d2.jpg';
import d3 from './diagrams/d3.jpg';
import d5 from './diagrams/d5.jpg';
import d6 from './diagrams/d6.jpg';
import d4 from './diagrams/d4.jpg';
import d7 from './diagrams/d7.jpg';
import d8 from './diagrams/d8.jpg';
import d9 from './diagrams/d9.jpg';
import d10 from './diagrams/d10.jpg';
import d11 from './diagrams/d11.jpg';
import d12 from './diagrams/d12.jpg';
import d13 from './diagrams/d13.jpg';
import d14 from './diagrams/d14.jpg';
import posenc from './diagrams/posenc.png';
import Md from 'react-markdown';
import { CodeBlock } from './codeblock';
import * as datastuff from './content/datastuff';
import * as attention from './content/attention';
import * as gpt from './content/gpt';
import gptzip from './llm.zip';



export function Intro() {

    const gpt3b1bcaption = <a href='https://www.youtube.com/watch?v=wjZofJX0v4M'>
        <u>But what is a GPT? Visual intro to transformers | Chapter 5, Deep Learning</u>
    </a>;

    return (
        <Section>
            <Row>
                <TextBlock side='left'>{Object.keys(introcontent).map(val => <Md>{introcontent[val]}</Md>)}</TextBlock>
                <Diagram src={gpt3b1b} caption={gpt3b1bcaption} height={'20rem'} side={'right'}/>
            </Row>
        </Section>
    );
}



export function TransformerStuff({ mode }) {
    return (
        <Section title={'Transformers'} mode={mode}>
            <TextBlock side='left'><Md>{transformercontent.p1}</Md></TextBlock>
            
            <br />
            <Diagram src={d1} width={'50vw'} side={'left'} caption={'A transformer'} center scale/>
            <br />
            
            <TextBlock side='left'><Md>{transformercontent.p2}</Md></TextBlock>
            
            <Section title={'Tokenising, Encoding, Embedding'} mode={mode} size='h3'>
                <Row vcenter>
                    <TextBlock >
                        <Md>{transformercontent.p3}</Md>
                    </TextBlock>
                    <Diagram src ={d5} side='right' height={'13rem'} caption='Tokenising Text'/>
                </Row>
                <br />
                <br />
                <Row>
                    <Diagram src ={d6} side='left' height={'20rem'} caption='Tokenising Text' scale/>
                    <CodeBlock mode={mode} text={transformercontent.codectxt1}>{transformercontent.code1}</CodeBlock>
                </Row>
                <br />
                <br />
                <Row>
                    <Diagram src ={d3} side='left' height={'17rem'} caption='Encoding Text' scale/>
                    <TextBlock >
                        <Md>{transformercontent.p4}</Md>
                        <Md>{transformercontent.p5}</Md>
                    </TextBlock>
                </Row>
            </Section>

            <Section title='Context Matters!' mode={ mode } size='h3'>
                <TextBlock>
                    <Md>{transformercontent.context1}</Md>
                </TextBlock>
                <br />
                <Diagram center scale src={d4} height={'18rem'} side={'right'} caption={'Context Windows'}/>
            </Section>

            <Section title={'On positions...'} size='h3' mode={mode}>
                <TextBlock>
                    <Md>{transformercontent.pos}</Md>
                </TextBlock>
                <Diagram center scale src={d7} height={'18rem'} side={'right'} caption={'Position Matrix'}/>
                
                <CodeBlock>{transformercontent.poscode}</CodeBlock>
                
                <Row>
                    <Diagram center scale src={posenc} height={'20rem'} side={'left'} caption={'Positional Embedding'}/>
                    <TextBlock>
                        <Md>{transformercontent.poscodetext}</Md>
                    </TextBlock>
                </Row>
            </Section>

        </Section>
    );
}


export function DataStuff({ mode }) {
    return (
        <Section title='The Data (text)' mode={mode}>
            <TextBlock>
                <Md>{datastuff.d1}</Md>
            </TextBlock>
            <CodeBlock>
                {datastuff.c1}
            </CodeBlock>
            <TextBlock>
                <Md>{datastuff.d2}</Md>
            </TextBlock>
        </Section>
    );
}


export function Attention({ mode }) {
    return (
        <Section title='Pay Attention!' mode={mode}>
            <Row>
                <TextBlock>
                    <Md>{attention.intro}</Md>
                </TextBlock>
                <Diagram src={d9} height={'18rem'} caption={'Attention'} side={'right'}/>
            </Row>
            <TextBlock>
                <Md>{attention.motivation1}</Md>
            </TextBlock>
            <Diagram src={d8} height={'18rem'} center scale caption={'Identical embeddings'} side={'left'}/>
            <TextBlock>
                <Md>{attention.motivation2}</Md>
            </TextBlock>
            <Section title={'Attention: Mechanism'} size='h3' mode={mode}>
                <TextBlock>
                    <Md>{attention.preintro}</Md>
                </TextBlock>
                <Row>
                    <TextBlock side='left'>
                        <Md>{attention.mech1}</Md>
                    </TextBlock>
                    <Diagram src={d10} height={'15rem'} caption={'Adjectives'} side={'right'}/>
                </Row>
                <Row vcenter>
                    <Diagram src={d11} height={'15rem'} caption={'Queries and Keys'} side={'left'}/>
                    <TextBlock>
                        <Md>{attention.mech2}</Md>
                    </TextBlock>
                </Row>
                <Diagram scale src={d12} height={'25rem'} caption={'Computing delta'} side={'left'} center/>
                <TextBlock>
                    <Md>{attention.mech3}</Md>
                </TextBlock>
                <Diagram src={d13} height={'20rem'} caption={'Computing delta'} side={'left'} center/>
                <TextBlock>
                    <Md>{attention.mech4}</Md>
                </TextBlock>
            </Section>
            <Section title={'Attention: Code'} size='h3' mode={mode}>
                <CodeBlock mode={mode} text={attention.codetext}>{attention.attcode}</CodeBlock>
                <br />
                <br />
                <br />
                <Divider mode={mode}/>
                <TextBlock>
                    <Md>{attention.attcodeexplanation1}</Md>
                </TextBlock>
                <CodeBlock>{attention.codep1}</CodeBlock>
                <br />
                <br />
                <br />
                <CodeBlock text={attention.attcodeexplanation2}>{attention.codep2}</CodeBlock>
                <Row>
                    <TextBlock>
                        <Md>{attention.attcodeexplanation3}</Md>
                    </TextBlock>
                    <CodeBlock>{attention.codep3}</CodeBlock>
                </Row>
                <Row>
                    <TextBlock>
                        <Md>{attention.attcodeexplanation4}</Md>
                    </TextBlock>
                    <CodeBlock>{attention.codep4}</CodeBlock>
                </Row>
                <Row>
                    <TextBlock>
                        <Md>{attention.attcodeexplanation5}</Md>
                    </TextBlock>
                    <CodeBlock>{attention.codep5}</CodeBlock>
                </Row>
            </Section>
            <Section title={'Multi-head Attention'} size='h3' mode={mode}>
                <TextBlock>
                    <Md>{attention.mh1}</Md>
                </TextBlock>
                <Diagram src={d14} height={'15rem'} center scale caption={'Multi-head attention'}/>
                <CodeBlock mode={mode} text={'Code implementing parallel / multi attention heads:'}>
                    {attention.mhcode}
                </CodeBlock>
                <TextBlock>
                    <Md>
                        Notice the projection at the end there, which puts the tokens back in the `d_embed` dimensional space.

                        Thus, at the end of an attention block, the shape of the tokens remains unchanged.
                    </Md>
                </TextBlock>
            </Section>
            <br />
            <br />
            <br />
            <TextBlock>
                <Md>{attention.end}</Md>
            </TextBlock>
        </Section>
    );
}

export function GPT({ mode }) {
    return (
        <Section title={'Putting it all together'} mode={mode}>
            <TextBlock>{gpt.intro}</TextBlock>
            <br />
            <br />
            <br />
            <Section title={'Generation'} size='h3' mode={mode}>
                <Row vcenter>
                    <TextBlock>
                        <Md>{gpt.gen}</Md>
                    </TextBlock>
                    <Diagram src={d2} center width={'35rem'} caption={'Generation'} side={'right'}/>
                </Row>
            </Section>
        </Section>
    );
}


export function FinishedCode({ mode }) {
    return (
        <Section title={'Final Code'} mode={mode}>
            <Row vcenter>
                <TextBlock>
                    <Md>{gpt.aboutdown}</Md>
                </TextBlock>
                <DownloadButton 
                    mode={mode}
                    filepath={gptzip}
                    filename={'mini_llm'}
                />
            </Row>
        </Section>
    );
}
