import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import * as themeing from 'react-syntax-highlighter/dist/esm/styles/prism';
import Select from 'react-select';
import { useState } from 'react';
import './App.css';
import clippy from './clipboard.svg';
import {motion} from 'framer-motion';
import Md from 'react-markdown';
// import './../fonts/FiraCode-VariableFont_wght.ttf'



const clipvars = {
    'light' : {
        'filter': 'invert(0%) sepia(0%) saturate(0%) hue-rotate(106deg) brightness(92%) contrast(104%)',
        // 'border': '0.05rem solid rgb(255, 255, 255)'
    },
    'dark' : {
        'filter': 'invert(100%) sepia(100%) saturate(0%) hue-rotate(161deg) brightness(101%) contrast(100%)',
        // 'border': '0.05rem solid rgb(255, 255, 255)'
    },
};


export function CodeBlock({ children, mode, text, }) {

    const options = [];
    Object.keys(themeing).forEach(val => options.push({
        label : val,
        value : val
    }));

    const [theme, setTheme] = useState(themeing.synthwave84);
    // const [theme, setTheme] = useState(themeing.atomDark);

    const [copied, setCopied] = useState(false);

    return (
        <div style={{width : '100%'}}>
            <Md>{text ? text : null}</Md>
            <div style={{display : 'flex', flexDirection : 'column'}}>
                <div
                    className='clipboardbutton'
                    onClick={() => {
                        setCopied(true);
                        navigator.clipboard.writeText(children);
                    }}
                >
                    {copied ? 'Copied!' : 'Copy'} 
                    <motion.img 
                        src = {clippy} 
                        className='clippy'
                        variants={clipvars}
                        initial={mode}
                        animate={mode}
                    />
                </div>
                <SyntaxHighlighter 
                    language='python' 
                    style={theme}
                    customStyle={{
                        fontFamily : 'codefont',
                        fontSize : '1rem',
                        borderRadius : '1rem',
                        // width : '100%'
                    }}
                    showLineNumbers
                >
                    {children}
                </SyntaxHighlighter>
            </div>
            <Select options={options} onChange={e => {
                console.log(e);
                setTheme(themeing[e.value])
            }}/>
        </div>
    );

}