export const preintro = 
`
Attention works, in essence, by computing the **change for a token embedding** as the **weighted sum** of all other 
embeddings in the context window. In this section, we see how exactly this weighted sum is calcualted. 
`.trim();


export const intro = 
`
Right, let's dive into the heart of LLMs, attention! Attention, as outlined in the original paper, is a 
mechanism that allows tokens to influence each other. Specifically, each embedding of each token in the context window undergoes 
a change that depends on all the other tokens present in the context window.
`.trim();



export const motivation1 = 
`
Before we look at how attention actually works, let us see the motivation behind this mechanism. 
Imagine a model that uses only MLPs or a Feed Forward Neural Network. 
For such a model, in the following two sentences:

`.trim();


export const motivation2 = 
`
The embeddings for the word \`friends\` would be identical, as they both the word embedding and position 
embedding are the same. The model won't be able to discern the nuance of this word (being associated 
with Ron and Hermione when preceded by \`Harry's\` and with Crabbe and Goyle when preceded by \`Draco's\`) 
if there is no mechanism to change these embeddings depending on other tokens.


`.trim();


export const mech1 =
`
Consider the following sentence.

In it, the token \`stone\` might be interested in adjective tokens, and change its embedding based only on those that describe 
it. Thus, **we need a way to assign weights (importance) to each token in the window**. The way we do this is using 
vector representations of these questions and responses; also called *queries* and *keys*.
`.trim();


export const mech2 =
`
Once we have these queries and keys, which are just vectors of real numbers, we find how well each key *"attends"* to 
this query, by taking dot products. 

Once we have the dot products, we can easily assign weights to each token by applying softmax, which clamps all values 
between 0 and 1 in such a way that the sum is exactly 1. The change for this token is then computed by taking a weighted sum 
of all the other tokens.
`.trim();



export const mech3 =
`
Naturally, the question is how do we come up with these query and key vectors? 
These vectors are actually produced by the model itself, and as part of the training process, the model 
learns the best vectors to come up with. In essence, the queries and keys are computed by taking matrix products:
`.trim();

export const mech4 =
`
The size of the queries / keys is also called the head size. 

**Important thing to note:** we use another matrix called the *value* matrix to project 
the embeddings of all tokens from embedding dimension to head size, and **then** take their weighted sum.
The reason for this is explained later, and how to get back to embedding dimension as well.

One set of Q, K and V matrices constitutes one attention "head". In practice, multiple attention heads are used 
in parallel as we will see later.

`.trim();



export const codetext = 
`
The following snippet defines a single attention head that performs attention operations on a batch of samples.
`.trim();

export const attcode = 
`

class AttentionHead(nn.Module):
    
    def __init__(self, 
            in_channels : int, 
            context_size : int, 
            head_size : int, 
        ) -> None:
        super().__init__()
        
        self.key = nn.Linear(in_channels, head_size, bias = False)
        self.query = nn.Linear(in_channels, head_size, bias = False)
        self.value = nn.Linear(in_channels, head_size, bias = False)
        
        self.t = context_size
        self.hs = head_size
        
        self.register_buffer('mask', torch.tril(torch.ones(self.t, self.t)))
    
    def forward(self, x):
        x # b t c
        
        b, t, c = x.shape
        
        k = self.key(x) # b t hs
        q = self.query(x) # b t hs
        
        #Standardization term
        weights = (c ** -0.5) * q @ k.transpose(-2, -1) # b t hs @ b hs t = b t t

        #This masking is explained later
        weights = weights.masked_fill(self.mask[:t, :t] == 0, float('-inf'))

        weights = F.softmax(weights, dim = -1)
        
        v = self.value(x) # b t hs
        out = weights @ v # b t t @ b t hs = b t hs
        
        return out


atthead = AttentionHead(
    in_channels=4, 
    context_size=16, 
    head_size=8
)

x = torch.rand((1, 16, 4))
print(x.shape) # (1, 16, 4)

x = atthead(x)
print(x.shape) # (1, 16, 8)

`.trim();


export const attcodeexplanation1=
`
Let's go over some of the important lines. These lines define our *K*, *Q*, and *V* matrices.
`.trim();

export const codep1=
`
self.key = nn.Linear(in_channels, head_size, bias = False)
self.query = nn.Linear(in_channels, head_size, bias = False)
self.value = nn.Linear(in_channels, head_size, bias = False)
`.trim();


export const attcodeexplanation2=
`
Here, we calculate our keys and queries of all the tokens in the context window in one go.
`.trim();

export const codep2=
`
k = self.key(x) # b t hs
q = self.query(x) # b t hs
`.trim();

export const attcodeexplanation3=
`
This matrix multiplication gives us the dot products between all the queries and all the keys of all the tokens.
Notice that this is a matrix of shape \`(context_size, context_size)\`. It is then multiplied by a standardization term.
`.trim();

export const codep3=
`
weights = (c ** -0.5) * q @ k.transpose(-2, -1) # b t hs @ b hs t = b t t
`.trim();


export const attcodeexplanation4=
`
Before applying softmax to these weights, we first set the lower triangle of the matrix to -infinity. This is due to the 
context windows being trained simulatneously. Hence, we don't want a given token to be affected by a token that is yet to be 
generated / future token. A mode detailed explanation can be found [here](https://youtu.be/eMlx5fFNoYc?si=CinLKUHh4qhnmFRP&t=670).
`.trim();

export const codep4=
`
weights = weights.masked_fill(self.mask[:t, :t] == 0, float('-inf'))

weights = F.softmax(weights, dim = -1)
`.trim();


export const attcodeexplanation5=
`
Finally, the output is calculated by first calculating the *values* of each of the tokens (now in a \`head_size\` dimensional space) 
and taking weighted sum, by multiplying with the weights matrix.
`.trim();

export const codep5=
`
v = self.value(x) # b t hs
out = weights @ v # b t t @ b t hs = b t hs
`.trim();


export const mh1=
`
As we saw, a single head of attention consists of a key, query and value matrix that is common for all tokens. 
A natural question then is if a single query matrix can generate all the required queries a given token might need? 
One way to deal with this would be to make several attention heads at each layer, and then take the average of all the 
outputs. But in practice, this would lead to huge matrices and massive models.

Instead, a common practice is to construct the final head vector by concatenating outputs from several heads simultaneously:
`.trim();



export const mhcode=
`
class ParallelAttentionHead(nn.Module):
    
    def __init__(self, 
            in_channels : int, 
            context_size : int, 
            head_size : int, 
            nheads : int, 
        ) -> None:
        super().__init__()
        
        self.heads = []
        for _ in range(nheads):
            self.heads.append(
                AttentionHead(
                    in_channels=in_channels,
                    context_size=context_size,
                    head_size = head_size // nheads, # assigning a chunk of the final head size to each head
                )
            )
        self.heads = nn.ModuleList(self.heads)
        self.projector = nn.Linear(head_size, in_channels)
        
    
    def forward(self, x):
        x # b t c
        out = torch.concat(list(map( lambda head : head(x), self.heads )), dim = -1)
        out = (self.projector(out)) # b t c
        return out
        
`.trim();

