export const gen =
`
Once you have a model that can output probabilities of the next word in the sequence, it is trivial to make a generative 
model. All that we do is simply take the probability distribution, sample it to get a token, append this to the original prompt 
and treat this as the new prompt. You can keep doing this to generate as many tokens as you want!
`.trim();






export const archpy=
`
import torch
import torch.nn as nn
import torch.nn.functional as F


class AttentionHead(nn.Module):
    
    def __init__(self, in_channels : int, context_size : int, head_size : int, droprate : float = 0.2, device : str = 'cpu') -> None:
        super().__init__()
        
        self.key = nn.Linear(in_channels, head_size, bias = False, device = device)
        self.query = nn.Linear(in_channels, head_size, bias = False, device = device)
        self.value = nn.Linear(in_channels, head_size, bias = False, device = device)
        
        self.t = context_size
        self.hs = head_size
        self.device = device
        
        self.register_buffer('mask', torch.tril(torch.ones(self.t, self.t, device = device)))
        self.dropout = nn.Dropout(droprate)
    
    def to(self, device : str):
        val = super().to(device)
        self.mask = self.mask.to(device)
        self.key = self.key.to(device)
        self.query = self.query.to(device)
        self.value = self.value.to(device)
        self.device = device
        return val
    
    def forward(self, x):
        x # b t c
        
        b, t, c = x.shape
        
        k = self.key(x) # b t hs
        q = self.query(x) # b t hs
        
        weights = q @ k.transpose(-2, -1) * (c ** -0.5) # b t hs @ b hs t = b t t
        weights = weights.masked_fill(self.mask[:t, :t] == 0, float('-inf'))
        weights = F.softmax(weights, dim = -1)
        weights = self.dropout(weights)
        
        v = self.value(x) # b t hs
        out = weights @ v # b t t @ b t hs = b t hs
        
        return out
        


class ParallelAttentionHead(nn.Module):
    
    def __init__(self, in_channels : int, context_size : int, head_size : int, nheads : int, droprate : float = 0.2, device : str = 'cpu') -> None:
        super().__init__()
        
        self.heads = []
        for _ in range(nheads):
            self.heads.append(
                AttentionHead(
                    in_channels=in_channels,
                    context_size=context_size,
                    head_size = head_size // nheads,
                    droprate = droprate,
                    device = device
                )
            )
        self.heads = nn.ModuleList(self.heads)
        self.dropout = nn.Dropout(droprate)
        self.projector = nn.Linear(head_size, in_channels, device = device)
        self.norm = nn.LayerNorm(in_channels, device = device)
        
    
    def to(self, device : str):
        val = super().to(device)
        self.projector = self.projector.to(device)
        self.norm = self.norm.to(device)
        for i, each in enumerate(self.heads):
            self.heads[i] = each.to(device)
        return val
    
    def forward(self, x):
        x # b t c
        # out = self.norm(x)
        out = torch.concat(list(map( lambda head : head(x), self.heads )), dim = -1)
        out = self.dropout(self.projector(out)) # b t c
        # out = out + x
        return out
        



class FFN(nn.Module):
    
    def __init__(self, in_channels : int, efactor : int = 4, droprate : float = 0.2, device : str = 'cpu') -> None:
        super().__init__()
        
        self.norm = nn.LayerNorm(in_channels, device = device)
        
        self.ffn = nn.Sequential(
            nn.Linear(in_channels, efactor * in_channels, device = device),
            nn.ReLU(),
            nn.Linear(efactor * in_channels, in_channels, device = device),
            nn.Dropout(droprate),
        )
        
        self.device = device
    
    def to(self, device : str):
        val = super().to(device)
        self.ffn = self.ffn.to(device)
        self.norm = self.norm.to(device)
        return val
    
    
    def forward(self, x : torch.Tensor) -> torch.Tensor:
        x # b t c
        # out = self.norm(x)
        out = self.ffn(x) # b t c
        # out = x + out
        return out


class Block(nn.Module):
    
    def __init__(
            self,
            in_channels : int,
            context_size : int,
            head_size : int,
            nheads : int,
            droprate : float = 0.2,
            device : str = 'cpu',
        ) -> None:
        super().__init__()
        
        self.att = ParallelAttentionHead(
            in_channels = in_channels,
            context_size= context_size,
            head_size = head_size,
            nheads = nheads,
            droprate = droprate,
            device = device
        )
        
        self.attln = nn.LayerNorm(in_channels, device = device)
        
        self.ffn = FFN(
            in_channels = in_channels,
            efactor = 4,
            droprate = droprate,
            device = device
        )
        
        self.ffnln = nn.LayerNorm(in_channels, device = device)
        
    
    def to(self, device : str):
        val = super().to(device)
        self.att = self.att.to(device)
        self.ffn = self.ffn.to(device)
        self.ffnln = self.ffnln.to(device)
        self.attln = self.attln.to(device)
        return val
    
    def forward(self, x):
        x #b t c
        x = x + self.att(self.attln(x))
        x = x + self.ffn(self.ffnln(x))
        return x




def position_embedding(context_size : int, d_embed : int, N : int = 1e+4, device : str = 'cpu') -> torch.Tensor:
    embs = torch.arange(0, d_embed // 2, 1)
    embs = torch.unsqueeze(embs, -1)
    embs = torch.tile(embs, (1,2))
    embs = torch.flatten(embs)
    embs = torch.unsqueeze(embs, -1)
    embs = torch.tile(embs, (1, context_size))
    
    embs = embs * (2 / d_embed)
    embs = torch.pow(N, -embs)
    
    for each in range(context_size):
        embs[:, each] *= each
    
    for each in range(d_embed):
        if each % 2: embs[each] = torch.cos(embs[each])
        else: embs[each] = torch.sin(embs[each])
    
    embs = embs.to(device)
    return embs.T





class Transformer(nn.Module):
    
    def __init__(
            self, 
            vsize : int,
            d_embed : int,
            context_size : int,
            nblocks : int,
            head_size : int,
            nheads : int,
            droprate : float = 0.2,
            device : str = 'cpu',
        ) -> None:
        super().__init__()
        
        self.token_embedder = nn.Embedding(vsize, d_embed, device = device)
        # self.position_embedder = nn.Embedding(context_size, d_embed, device = device)
        self.device = device
        
        self.blocks = [
            Block(
                in_channels = d_embed,
                context_size = context_size,
                head_size = head_size,
                nheads = nheads,
                droprate = droprate,
                device = device
            ) for each in range(nblocks)
        ]
        
        self.blocks = nn.Sequential(*self.blocks)
        
        self.norm = nn.LayerNorm(d_embed, device = device)
        self.unembedder= nn.Linear(d_embed, vsize, device=device)
        
        self.context_size = context_size
        self.d_embed = d_embed
        
    
    
    def to(self, device : str):
        val = super().to(device)
        self.token_embedder = self.token_embedder.to(device)
        # self.position_embedder = self.position_embedder.to(device)
        self.norm = self.norm.to(device)
        self.unembedder = self.unembedder.to(device)
        self.blocks = self.blocks.to(device)
        self.device = device
        return val
    
    
    def forward(self, inputs : torch.Tensor, debug : bool = False) -> torch.Tensor:
        b, t = inputs.shape
        
        # tokens = self.token_embedder(inputs) + self.position_embedder(torch.arange(t, device=self.device))
        tokens = self.token_embedder(inputs) + position_embedding(t, self.d_embed, device=self.device)
        if debug: print(tokens.shape)
        tokens = self.blocks(tokens)
        if debug: print(tokens.shape)
        tokens = self.norm(tokens)
        if debug: print(tokens.shape)
        tokens = self.unembedder(tokens)
        if debug: print(tokens.shape)
        return tokens
    
    
    def generate_tokens(self, seed : torch.Tensor, max_tokens : int = 2000) -> torch.Tensor:
        
        tokens = seed # b n
        
        for each in range(max_tokens):
            print(f'Generating token {each+1}/{max_tokens}...', end = '\\r')
            
            tokensub = tokens[:, -self.context_size:] # b t (last t)
            
            logits = self(tokensub) # b t vsize
            
            logits = logits[:, -1, :] # b -1 vsize = b vsize
            
            probs = F.softmax(logits, dim = -1) # b vsize
            
            pred = torch.multinomial(probs, num_samples = 1) # b 1
            
            tokens = torch.concat((tokens, pred), dim = 1)
            
        
        print()
        return tokens

`.trim();


export const trainpy=
`
import torch
import torch.nn as nn
import torch.nn.functional as F
import pickle
import matplotlib.pyplot as plt

from typing import Callable
from arch import *


def read_data(folder : str) -> str:
    #Join all books' text into one:
    rawtext = ''
    for each in range(1, 8):
        with open(f'{folder}/hp{each}.txt', 'r', encoding = 'utf-8') as file:
            rawtext = file.read().strip()
    return rawtext

def tokenise(rawtext : str) -> list[str]:
    # Split by newline, join back with spaces
    rawtext = ' '.join(rawtext.split('\\n'))
    
    #Deal with all the special characters that might be in the text
    for each in "()[]!?.,{}'\\"-;:…_=+":
        rawtext = f' {each} '.join(rawtext.split(each))
    
    # Split into words
    rawtext = rawtext.split(' ') 
    print('Total no. of tokens in text: ', len(rawtext))
    
    #Convert all words to lower case:
    rawtext = list(map( lambda x: x.strip().lower(), rawtext))
    
    return rawtext

def make_encoding(tokens : list[str]) -> tuple[dict[str, int], dict[int, str]]:
    #Get all unique tokens:
    uniques = list(set(tokens))
    uniques.sort()
    print('Unique tokens in text: ', len(uniques))
    
    encoding, decoding = dict(), dict()
    
    #encoding and decoding based on index values
    for i, each in enumerate(uniques):
        encoding[each] = i
        decoding[i] = each
    
    return encoding, decoding


def load_text(
        encodingfile : str = None, folder : str = 'hp_data', device : str = 'cpu'
    ) -> tuple[torch.TensorType, int, Callable[[str], list[int]], Callable[[list[int]], str]]:
    fulltext = read_data(folder)
    
    if encodingfile is None:
        uniques = list(set(list(fulltext)))
        encoder = dict()
        decoder = dict()
        for i, each in enumerate(uniques):
            encoder[each] = i
            decoder[i] = each
        with open('encoding.enc', 'wb') as file:
            pickle.dump((uniques, encoder, decoder), file)
    else:
        with open(encodingfile, 'rb') as file:
            uniques, encoder, decoder = pickle.load(file)
    
    
    fulldata = torch.tensor([encoder[x] for x in list(fulltext)], dtype = torch.long, device = device)
    
    return fulldata, len(uniques), encoder, decoder
    






def get_batch(data : torch.Tensor, batch_size : int = 4, context_size : int = 8, device = 'cpu', expand : bool = False) -> tuple[torch.Tensor, torch.Tensor]:
    length = data.shape[0]
    
    
    starts = torch.randint(length - context_size - 1, (batch_size,))
    bx = [data[each : each + context_size] for each in starts]
    by = [data[each + 1 : each + 1 + context_size] for each in starts]
    
    batchx = torch.stack(bx).to(device)
    batchy = torch.stack(by).to(device)
    
    
    if expand:
        batchx = batchx.unsqueeze(-1)
        batchy = batchy.unsqueeze(-1)
    
    return batchx, batchy








@torch.no_grad()
def estimate_loss(model : nn.Module, data : torch.Tensor, batch_size : int, context_size : int, device : str = 'cpu', iters : int = 100):
    out = None
    model.eval()
    # for split in ['train', 'val']:
    losses = torch.zeros(iters, device = device)
    for k in range(iters):
        X, Y = get_batch(data, batch_size, context_size, device)
        ycap = model(X)
        b, t, c = ycap.shape
        loss = F.cross_entropy(ycap.view(b * t, c), Y.view(b * t))
        losses[k] = loss.item()
        print(f'\\tEval iter {k+1}/{iters}...', end = '\\r')
    out = losses.mean().item()
    model.train()
    print()
    return out


def train_transformer(
        data : torch.Tensor, 
        model : nn.Module,
        lr : float = 1e-3,
        batch_size : int = 32, 
        context_size : int = 8, 
        epochs : int = 10, 
        steps : int = 500,
        eval_iters : int = 100,
        device : str = 'cpu',
    ) -> list[float]:
    
    timeline = {
        'train' : [],
        'eval' : []
    }
    
    model.train()
    
    adam = torch.optim.AdamW(model.parameters(), lr = lr)
    
    for epoch in range(epochs):
        print(f'Epoch {epoch + 1}:')
        # l = estimate_loss(model, data, batch_size, context_size, device, eval_iters)
        # timeline['eval'].append(l)
        for each in range(steps):
            xb, yb = get_batch(data, batch_size, context_size, device)
            
            ycap = model(xb)
            
            b, t, c = ycap.shape
            
            loss = F.cross_entropy(ycap.view(b * t, c), yb.view(b * t))
            # l = loss.item()
            
            # model.zero_grad()
            adam.zero_grad()
            loss.backward()
            adam.step()
            
            print(f'\\tStep {each + 1}/{steps} : {loss.item():.5f}', end = '\\r')
            timeline['train'].append(loss.item())
        print()
        l = estimate_loss(model, data, batch_size, context_size, device, eval_iters)
        timeline['eval'].append(l)
        print(f'\\n\\tEval loss : {l:.5f}')
        print()
    
    model.eval()
    return timeline




if __name__ == '__main__':
    
    if torch.cuda.is_available(): print('CUDA is working...')
    else: print('CUDA NOT FOUND...')
    
    fulldata, uniques, encoder, decoder = load_text()
    
    context_size = 64
    
    gpt = Transformer(
        vsize = uniques,
        d_embed = 256,
        context_size = context_size,
        nblocks = 6,
        head_size = 64,
        nheads = 8,
        droprate = 0.25
    )
    
    
    
    # gpt : Transformer = torch.load('gpt.pth')
    
    gpt = gpt.to('cuda')
    
    print(sum([sum([x.numel() for x in each.parameters()]) for each in gpt.blocks])/1e6, 'M parameters')
    
    
    timeline = train_transformer(
        fulldata, gpt, 
        device = 'cuda',
        batch_size = 16 * 2 * 2,
        context_size = context_size,
        epochs = 50,
        steps = 100,
    )
    
    torch.save(gpt, 'gpt.pth')
    
    
    fig, ax = plt.subplots(1, 2, figsize = (12, 5))
    ax[0].plot(timeline['train'])
    ax[0].set_title('train')
    ax[1].plot(timeline['eval'])
    ax[1].set_title('eval')
    plt.show()

`.trim();


export const genpy=
`
import torch
import torch.nn as nn
import torch.nn.functional as F
import pickle

from typing import Callable
from arch import *






model : Transformer = torch.load('gpt.pth')
model = model.to('cuda')

with open('encoding.enc', 'rb') as file:
    _, encoder, decoder = pickle.load(file)

prompt = input('Enter a prompt: ')
prompt = prompt.strip()
prompt = [encoder[x] for x in list(prompt)]

gen = model.generate_tokens(torch.tensor([prompt], dtype = torch.long, device = 'cuda'), 1000)
gen = ''.join([decoder[x] for x in gen[0].tolist()])

with open('output.txt', 'w') as file:
    file.write(gen)

print("Generated:\\n")
print(gen)


`.trim();

