export const context1 =
`

One of the most important aspects of text is that context is extremely important. For instance, in the following text:

\`Harry's heart froze as he watched the dementor. It had no discernible shape, yet instilled a fear indescribable.\`

We as humans clearly understand that the word 'it' clearly refers to the dementor. This is because of the context 
we have about Harry being a human being (and thus wouldn't be referred by 'it'), and the emphasis of the former sentence 
being on the dementor rather than something like Harry's heart. Thus, context makes all the difference.

To capture this essence, or more specifically, to give our model a way to "understand" context, we will be training the model 
on "contexts" rather than individual tokens. The size of this context is an important parameter, you may have come 
across this term when LLMs are usually compared. 

The context size or window is the number of tokens that the model can 
"see" and use at a time, while predicting the next token in the sequence. The argument goes that larger contexts should 
make models perform better, but this comes with the cost of scalability and training issues. (More on this later.)

`.trim();


export const pos =
`
While we have a way for the model to understand meaning of the words, we haven't given it a way to *see* position. If we 
were to simply pass in these embeddings, the following two sentences:
\`\`\`
She was happy, not sad.

She was sad, not happy.
\`\`\`

would appear the same to the model, even though these have completely different meanings.

To embed the information of the positions of these tokens as well, we use **position embeddings**. 
Essentially, we use another matrix like the embedding table, where the \`i\`th column corresponds to the \`i\`th position vector 
and each vector has a length of embedding dimension.

Note : To know more about why we use this specific method, check out [this video](https://youtu.be/dichIcUZfOw?si=jBr_KbgTcHBN--Lu&t=278). 
This matrix and the embedding matrix must match in dimensions, as they will be added to form the final embeddings.
`.trim();


export const poscodetext =
`
We can generate positional embeddings given the context size and embedding dimensions easily. 
On the left, I have plotted the position matrix generated by \`position_embedding(64, 64)\`.

A few things of note:
- The positional embeddings DO NOT depend on the tokens, or the length of the sequence of tokens, only on the context window.
- In the visual representation of the the position vectors, each vertical strip is a single vector. Note how no two vectors are identical.
`.trim();

export const poscode =
`
def position_embedding(context_size : int, d_embed : int, N : int = 1e+2) -> torch.Tensor:
    embs = torch.arange(0, d_embed // 2, 1)
    embs = torch.unsqueeze(embs, -1)
    embs = torch.tile(embs, (1,2))
    embs = torch.flatten(embs)
    embs = torch.unsqueeze(embs, -1)
    embs = torch.tile(embs, (1, context_size))
    
    embs = embs * (2 / d_embed)
    embs = torch.pow(N, -embs)
    
    for each in range(context_size):
        embs[:, each] *= each
    
    for each in range(d_embed):
        if each % 2: embs[each] = torch.cos(embs[each])
        else: embs[each] = torch.sin(embs[each])
    
    return embs
`.trim();



